@import url('https://fonts.googleapis.com/css2?family=Arima+Madurai:wght@100;200;300;400;500;700&family=Fugaz+One&family=Noto+Sans:ital,wght@0,300;0,400;1,200;1,300;1,400&family=Work+Sans:wght@200;300;400&display=swap');

/*fonts*/
@font-face{
    font-family: "Finesse";
    src: local("Finesse"),
    url("./fonts/Finesse-Italic.otf") format("opentype");
    font-style: italic;
}

@font-face{
    font-family: "Finesse";
    src: local("Finesse"),
    url("./fonts/Finesse-Roman.otf") format("opentype");
    font-style: normal;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-Bold.ttf") format("truetype");
    font-weight: 500;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-SemiBold.ttf") format("truetype");
    font-weight: 400;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-Regular.ttf") format("truetype");
    font-weight: 300;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-Light.ttf") format("truetype");
    font-weight: 200;
}
/*font end*/


body {
    background-color: #f5f5f5;
    font-family: 'Manrope';
    font-weight: 300;
    overflow-x: hidden;
    color: rgb(65, 65, 65);
}

/*basic texts*/
.l-text{
    text-align: left;
    font-size: 36px;
    font-weight: 100;
    font-family: 'Finesse';
    font-style: italic;
    color: #5e68ee; /*767EF3*/
    margin-bottom: 20px;
}

.m-text{
    width: 100%;
    text-align: left;
    font-size: 14px;
    font-weight: 400;
    font-family: 'Manrope';
    color:#5e68ee; /*#2c2c2c*/
    margin-left: 5px;
}

.s-text{
    width: 100%;
    text-align: left;
    font-size: 12px;
    font-family: 'Manrope';
    font-weight: 300;
}

.text-container{
    display: flex;
    justify-content: left;
    align-items: center;
    width: 100%;
    height: 40px;
    border-bottom: 2px solid #2c2c2c;
}

.detail-container{
    margin: 0px;
    padding: 20px 10px;
    display: flex;
    flex-wrap: wrap;
    background-color: white;
    font-size: 14px;
    font-weight: 300;
    border-bottom: 1px dotted #2c2c2c;
}

/*main page*/
.container{
    display: flex;
    flex-flow: row wrap;
    padding: 0px 15px;
    justify-content: center;
}

.main-lp{
    position: absolute;
    z-index: -1;
    top: 175px;
    animation: rotation 10s infinite linear;
}

@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
}

.main-lp-img{
    width: 160px;
}
/*main page end*/


/* albums*/
.album-container{
    float:left;
    padding: 20px 20px 0px 0px;
    height: 150px;
}

.album-img{
    width: 120px;
}

@keyframes albumclicked {
    from {width: 110px}
    to {width: 140px}
}

.album-img-clicked{
    width: 140px;
    animation: albumclicked 0.5s;
}

.home-album-container{
    overflow-x: scroll;
    width: 100%;
    display: flex;
    flex-direction: row;
}
/* albums end*/


/* tracks */
.tracklist-container{
    margin-top: 20px;
    width: 100%;
    border-top: 1px dotted #2c2c2c;
    border-bottom: 1px dotted #2c2c2c;
    display: flex;
}

.tracklist-subcontainer{
    padding: 10px 10px;
    background-color: white;
}

.tracks {
    font-size: 8px;
    font-weight: 200;
}

.button_1{
    background-color: #5e68ee;
    border-radius: 30px;
    border: 1px solid #5e68ee;
    color: #ffffff;
    width: 150px;
    height: 25px;
    font-size: 12px;
    margin: auto;
    font-weight: 300;
    font-family: 'Manrope';
}

.preview-clicked{
    position: absolute;
    height: 350px;
    width: 100%;
    top: 150px;
    right: 0px;
    z-index: 1;
    display: flex;
    justify-content: center;
}

.preview-nclicked{
    display: none;
}

.preview-video{
    margin: 0px;
    width: 60%;
}
/* tracks end*/






.footer{
    width: 100%;
    text-align: center;
    font-family: 'Noto Sans';
    font-weight: 400;
    font-size: 10px;
    padding-top: 100px;
    padding-bottom: 20px;
}

.logo{
    width: 40px;
    padding: 20px;
}

.logo-bar{
    position: fixed;
    top: 0px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 70px;
    margin-bottom: 50px;
}

.onTop{
    position: fixed;
    top: 0px;
    width: 100%;
}

.main-img{
    width: 50%;
}

.loader{
    width: 100%;
    text-align: center;
    margin: 200px 0px;
}

.menu{
    width: 80%;
}

.menu-button{
    padding-top: 25px;
    padding-right: 30px;
    font-size: 12px;
    font-family: 'Manrope';
    font-weight: 500;
    z-index: 1;
    right: 30px;
}


@keyframes slidein {
    from {width: 0%}
    to {width: 80%}
}



.menu-clicked{ /* show menu */
    width: 80%;
    height:100%;
    background-color: #5e68ee;
    
    padding-right: 0px;
    padding-top: 0px;

    position: fixed;
    top: 0px;
    right: 0px;
    animation: slidein 0.5s;

    font-family: 'Finesse';
    font-weight: 100;
    color:white;
    z-index: 0;
    font-style: italic;
    font-size: 24px;
}

li{
    list-style: none;
}


.menu-nclicked{
    display: none;
}





.break {
    flex-basis: 100%;
    height: 0;
}

